import React from 'react';
import './styles/ContactPage.css';
import ContactInfo from "../Components/ContactInfo";
import SocialNetworksLink from "../Components/SocialNetworksLink";
import guillemets from "../Assets/guillemets.png";
import soulignage from "../Assets/soulignage.png"

export default class ContactPage extends React.Component{
    constructor(props){
        super(props);
    }

    render() {
        return(
            <div id="ContainerContactPage" className={this.props.transition}>
                <div id={"SocialNetworks"}>
                    <h1 id={"TitreLinks"}>Liens</h1>
                    <img id="soulignageLinks" src={soulignage} alt={"soulignageLinks"}/>
                    <div id={"SocialNetworksLinks-container"}>
                        <SocialNetworksLink Number={1} SocialNetwork={"Facebook"} Color={"color"} />
                        <SocialNetworksLink Number={2} SocialNetwork={"Instagram"} Color={"color"}/>
                        <SocialNetworksLink SocialNetwork={"Twitter"} Color={"color"}/>
                        <SocialNetworksLink SocialNetwork={"CentraleMarseille"} Color={"color"}/>
                    </div>
                </div>
                <div id={"Contacts"}>
                    <div id={"TitreContactsWhGuillemets"}>
                        <h1 id={"TitreContacts"}>Contacts</h1>
                        <img id="guillemetsContacts" src={guillemets} alt={"guillemmets"}/>
                        <img id="soulignageContacts" src={soulignage} alt={"soulignageContacts"}/>
                    </div>
                    <ContactInfo name={"Agathe RABACHOU"} poste={"Présidente UA"} phoneNumber={"06 10 53 82 27"} mailAdress={"arabachou@ec-m.fr"}/>
                    <ContactInfo name={"Nicolas OUZOULIAS"} poste={"Président BDE"} phoneNumber={"06 46 88 29 50"} mailAdress={"nouzoulias@ec-m.fr"}/>
                    <ContactInfo name={"Armand LOGNONÉ"} poste={"Responsable intégration"} phoneNumber={"06 78 89 14 34"} mailAdress={"alognone@ec-m.fr"}/>
                </div>
            </div>
        )
    }
}
